@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');

.a {
  background-color: rgb(9, 83, 9);
}

.brand-logo, h2 {
  font-family: 'Grand Hotel', cursive;
}

.mycard {
  padding-top: 40px;
}

.auth-card {
  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 30px auto;
}

/* label underline focus color */
.input-field input[type=text]:focus {
  border-bottom: 1px solid rgb(83, 255, 35) !important;
  box-shadow: 0 1px 0 0 rgb(144, 206, 136) !important;
}
.input-field input[type=password]:focus {
  border-bottom: 1px solid rgb(83, 255, 35) !important;
  box-shadow: 0 1px 0 0 rgb(144, 206, 136) !important;
}
/* //profile */
.gallery{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.item{
  width: 30%;
}

.home-card{
  max-width: 500px;
  height: max-content;  
  margin: 26px auto;
}
#toast-container{
  top:30px !important;
  right:20px !important;
  left:auto !important;
  
}
.like{
  cursor:pointer
}
.follow{
  margin:10px;
}
.collection-item{
  width: 100%;
  cursor: pointer;
  color: black;
}

/* nav */
.iconMesg{
  width: 46px;
  height: 21px;  
  padding: 0;
  margin: 0;
}
.icons{
 display: flex; 
 align-items: center;
}
.icon{
  margin-left: 10px;
  cursor: pointer;
}
.iconImage{
  width: 20px;
  height: 20px;  
  margin-top: 20px;
}
.counters{
  width: 13px;
  height: 14px;
  background-color: red;
  padding: 5px;
  border-radius: 5px;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 11px;
  left: -5;
}