.messages {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: none;
  }

  .input-f input[type=text]:focus {
    border-bottom: none !important;
    box-shadow: none !important;
    outline: none;
}
.message{
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}
.messageTop{
  display: flex;

}

.messageImg{
  width: 32px;
  height:32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.messageText{
 padding: 10px;
 border-radius: 20px; 
 background-color: cadetblue;
color: white;
 max-width: 300px;
}
.messageButton{
  font-size:12px;
  margin-top: 10px;
}
.message.own{
  align-items: flex-end;
}
.message.own .messageText{
  background-color: rgb(106, 131, 57);
 
}
.chatBoxBottom{
  margin-top: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.chatMessageInput{
  width: 80%;
  height: 90px;
  padding: 5px;
}
.chatSubmitButton{
  
  width: 70px;
  height:40px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: teal;
  color: white;
  font-size: 18px;
}

/* chatBox */
.chatOnlineBox{
  width:20%;
}
.chatOnlineName{
  padding-left: 38px;
  padding-right: 10px;
}
/* chat online */
.chatOnlineImgContainer{
  position: absolute;
  margin-top: 10px;
}
.chatOnlineFriend{
  display: flex;
  align-items: center;
  font-weight: 500;
  cursor: pointer;
}
.chatOnilneImg{
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px solid white;
}
.chatOnlineBadge{
  position: absolute;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: limegreen;
  top: 2px;
  right: 2px;
}
.noConv{
  position:absolute;
  top:20%;
  font-size:50px;
  color:rgb(224,220,220);
  /* text-align:center; */
  cursor:default;
}
